import test from '../../components/test.vue'
import a from '../../components/a.vue'
import b from '../../components/b.vue'
export default {
	data() {
		return {
			imgUrl:[],
			title:"你好呀"
		}
	},
	methods: {
		chooseImage(){
			uni.chooseImage({
				count:5,
				success:(res)=> {
					console.log("图片上传成功",res)
					this.imgUrl = res.tempFilePaths
					// console.log(this.imgUrl)
				}
			})
		},
		preview(current){
			// console.log(current)
			uni.previewImage({
				current:current,
				urls:this.imgUrl,
				// 设置是否可循环预览
				loop:true
			})
		},
		getemit(n){
			console.log("子组件传来的值",n)
		}
	},
	onLoad() {






	},
	components:{
		test:test,
		aa:a,
		bb:b
	}
}
